        font-size: 15px;
        left: 12px;
        top: 17px;
    }
}
.kode-alert .closed{
    position: absolute;
    right: 3px;
    text-decoration: none;
    font-weight: bold;
    top: 0px;
    font-size: 20px;
    color: rgba(255, 255, 255, 0.5);
    padding: 4px;
}

.kode-alert .closed:hover{
    color: #fff;
}

.kode-alert-click{
    cursor: pointer;
    padding-right: 12px;
}

.kode-alert .primary{
    outline: 0;
    border: none;
    background: rgba(0, 0, 0, 0.4);
    color: inherit;
    border-radius: 3px;
    padding: 4px 10px;
}
.kode-alert .cancel{
    outline: 0;
    border: none;
    background: rgba(255, 255, 255, 0.4);
    color: rgba(0, 0, 0, 0.8);
    border-radius: 3px;
    padding: 4px 10px;
}
.kode-alert .primary:hover, .kode-alert .cancel:hover{
    opacity: 0.9;
}

