/* Widget Map */
.gmaps,
.gmaps-panaroma{
  height:300px;
  background: #6699cc;
  border-radius: 3px;
}
.gmaps-overlay{
  display:block;
  text-align:center;
  color:#fff;
  font-size:20px;
  line-height:40px;
  opacity:0.8;
  background:#4477aa;
  border:solid 3px #336699;
  border-radius:4px;
  box-shadow:2px 2px 10px #333;
  text-shadow:1px 1px 1px #666;
  padding:0 4px;
}

.gmaps-overlay_arrow{
  left:50%;
  margin-left:-16px;
  width:0;
  height:0;
  position:absolute;
}
.gmaps-overlay_arrow.above{
  bottom:-15px;
  border-left:16px solid transparent;
  border-right:16px solid transparent;
  border-top:16px solid #336699;
}
.gmaps-overlay_arrow.below{
